@btn-font-weight: 300;

body, label, .checkbox label {
  font-weight: 300;
}

#navbar-head {
  background-image: url('/image/bg-header.png');
  background-repeat: repeat-x;
  min-height: 100px;
  margin-bottom: 0 !important;
  border: none !important;
  border-radius: 0 !important;
  -moz-border-radius: 0 !important;

}

#navbar-head .navbar-nav li a {
  line-height: 60px;
}

@import "bootstrap/bootstrap";
@import "bootstrap/variables";
@import "typography";
@import "mixins";

// Extend bootstrap
@media (min-width: @screen-sm-min) {
  .row-eq-height {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
  }
}

.btn-white {
  .button-variant(#000, #fff, #fff);
}

.btn-gray {
  .button-variant(#808080, #dadada, #dadada)
}

.btn-transparent-border {
  .button-variant(#000, #fff, rgba(0, 0, 0, 0.4));
  border-width: 3px;
}

.btn-facebook {
  .button-variant(#fff; #3b579d; #3b468c);
}

.btn-google {
  .button-variant(#fff; #d62020; #d62020);
}

.btn-blue {
  .button-variant(#fff, #223577, #223577)
}

.btn-huge {
  .button-size(10px; 24px; @font-size-base; @line-height-base; @border-radius-base);
}

.btn-icon {
  .button-size(4px; 10px; @font-size-base; @line-height-base; @border-radius-base);
}

.panel-contests {
  .panel-variant(transparent, #fff, #223577, transparent);
  border: none;
  .panel-heading {
    .background-banner(url('/image/bg-header.png'));
  }
  .panel-body {
    padding-bottom: 0;
  }
}

body {
  background-color: #f7f7f7;
}

body, label, .checkbox label {
  font-weight: 300;
}

img {
  max-width: 100%;
}

.navbar-brand {
  padding: 15px 10px;
  img {
    height: @navbar-height - 32px;
  }
}

// Fix .nav
.nav > li > p.navbar-btn {
  padding-left: 15px;
  padding-right: 15px;
}

.navbar-default {
  background-image: url('/image/bg-header.png');
  background-repeat: no-repeat;
}

.row-has-2-children {
  > .col-xs-6 {
    &:nth-child(1) {
      padding-right: @grid-gutter-width / 4;
    }
    &:nth-child(2) {
      padding-left: @grid-gutter-width / 4;
    }
  }
  @media (min-width: @screen-sm-min) {
    > .col-sm-6 {
      &:nth-child(1) {
        padding-right: @grid-gutter-width / 4;
      }
      &:nth-child(2) {
        padding-left: @grid-gutter-width / 4;
      }
    }
  }
  @media (min-width: @screen-md-min) {
    > .col-md-6 {
      &:nth-child(1) {
        padding-right: @grid-gutter-width / 4;
      }
      &:nth-child(2) {
        padding-left: @grid-gutter-width / 4;
      }
    }
  }
  @media (min-width: @screen-lg-min) {
    > .col-lg-6 {
      &:nth-child(1) {
        padding-right: @grid-gutter-width / 4;
      }
      &:nth-child(2) {
        padding-left: @grid-gutter-width / 4;
      }
    }
  }
}

.notification {
  margin: 0;
}

// Banner on homepage.
#content-wrapper {
  min-height: 100vh;
  min-height: ~"calc(100vh - " (@navbar-height + 75px) ~")";
}

.bars {
  @media (min-width: @grid-float-breakpoint) {
    display: none;
  }
}

@import 'auth';
@import 'cards';
@import 'stats';
@import 'slider';
@import 'pills';
@import 'banner';
@import 'footer';
@import 'overlay';
@import 'contest';
@import 'comparator';
@import 'helpers';
@import 'profile';

.modal-compare {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
}