.text-white {
  color: #fff;
}

.text-orange {
  color: #ef752a;
}

// Text align

@media only screen {
}

/* Define mobile styles */

@media only screen and (max-width: @screen-xs-max) {
  .text-center-xs {
    text-align: center;
  }

  .text-left-xs {
    text-align: left;
  }

  .text-right-xs {
    text-align: right;
  }
}

